.promo {
	position: relative;
	width: 100%;
	height: 100vh;

	&__bg {
		position: absolute;
		overflow: hidden;
		left: 0;
		top: 0;
		width: 100%;
	}

	&__img {
		width: auto;
		height: 100vh;
		background-size: cover;
		background-position: 50%;

		&--retail {
			background-image: url(../../img/images/2.jpg);

		}

		&--wholesale {
			background-image: url(../../img/images/3.jpg);
		}
	}

	&__item {
		position: absolute;
		z-index: 0;
		top: 0;
		height: 100vh;
		width: 100%;
		color: #FFF;
		-webkit-transition: background-color .35s;
		transition: background-color .35s;

		&:hover {
			color: #FFF;
			background: rgba(0, 0, 0, 0.4);
		}
		

		&__content {
			position: absolute;
			left: 40px;
			right: 40px;
			top: 20%;
			text-align: left;


			&__icon {

			}

			&__title {
				display: block;
				margin-bottom: 10px;
				font-weight: 400;
				font-size: 26px;
				line-height: 1.33;
				text-transform: uppercase;
				letter-spacing: .04em;
				margin: 0 auto;
				max-width: 300px;
			}

			&__text {
				display: block;
				margin: 0 auto;
				max-width: 300px;
				font-size: 14px;
			}

			&__link {
				display: block;
				margin: 20px auto;
				max-width: 300px;
				font-size: 18px;
				z-index: 12;



				&:hover {
					color: #000;
				}
			}
		}
	}
}


@media (max-width: @screen-sm) {
	.promo {
		height: 50vh;

		&__img {
			height: 50vh;
		}

		&__item {
			height: 50vh;
		}
	}
}

@media (max-width: 760px) {
	.promo {
		height: 100vh;

		&__img {
			height: 50vh;
		}

		&__item {
			height: 50vh;
		}

		&--retail {
			top: 0;
		}

		&--wholesale {
			top: 50vh;
		}
	}
}

.wrapper {
	width: 100%;
	overflow: hidden;
}




// Catalogue

.catalogue {

	&__item {

		a {
		width: 100%;
		height: 100%;
		display: block;

		}

		&__img img {
			width: 100%;
		}

		&__content {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			height: 100%;
			color: #fff;
			text-align: center;

			&:hover {
				background: rgba(0, 0, 0, 0.4);
			}

			&__title {
				position: absolute;
				bottom: 10%;
				left: 0;
				right: 0;
				font-size: 25px;
			}

			&__articulus {
				display: block;
				position: absolute;
				bottom: 5%;
				left: 0;
				right: 0;
			}
		}
	}
}






.footer {
	padding: 40px 15px 30px;
		

	& a {
		color: #333;

		&:hover {
			text-decoration: none;
			color: #777;
		}
	}

	&__list ul{
		list-style: none;
		padding-left: 0;
	}
}


.social {


	.vk {
		display: inline-block;
		background-image: url(../../img/svg/icons.svg);
		background-position: 40px 0px;
		background-size: cover;
		height: 30px;
		width: 30px;
	}

	.instagram {
		display: inline-block;
		background-image: url(../../img/svg/icons.svg);
		background-position: 85px 1px;
		background-size: cover;
		height: 30px;
		width: 30px;
	}
}














